* {
    margin: 0;
    padding: 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
body
{
    background:url('/background.gif') no-repeat center center fixed;
    background-size: 100% 100%;
}
.Font
{
    font-size:30px;
    font-family:Arial, Helvetica, sans-serif;
    color:#00F;
    text-align:center;
    vertical-align:middle;
    margin-top: 66px;
}
.col-md-1
{
    width:6.333%;
}
.col-md-6
{
    margin-left:5%;
    width:47%;
}

input[type="text"]#nhapSoHang::-webkit-input-placeholder { /* Chrome/Opera/Safari */
    color: #d4c8ca;
}

input[type="text"]#nhapSoHang::-moz-placeholder { /* Firefox 19+ */
    color: #d4c8ca;
}

input[type="text"]#nhapSoHang:-ms-input-placeholder { /* IE 10+ */
    color: #d4c8ca;
}

input[type="text"]#nhapSoHang:-moz-placeholder { /* Firefox 18- */
    color: #d4c8ca;
}


#nhapSoHang
{
    margin-top: 7px;
}
label
{
    max-width: 170px;
}
.btn-circle {
    margin-bottom:27px;
    width: 49px;
    height: 49px;
    text-align: center;
    padding: 5px 0;
    font-size: 20px;
    line-height: 2.00;
    border-radius: 30px;
}


ol, ul {
    padding-left: 30px;
}

.board-row:after {
    clear: both;
    content: "";
    display: table;
}

.status {
    margin-bottom: 10px;
}

.square {
    background: #fff;
    border: 1px solid #999;
    float: left;
    font-size: 20px;
    font-weight: bold;
    line-height: 30px;
    height: 30px;
    width: 30px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;

}

.square:focus {
    outline: none;
}

.square:hover {
    background-color: #DADADA;
}

.kbd-navigation .square:focus {
    background: #ddd;
}

.game {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    padding-left: 110px;
}



.green {
    color: green;
}

.red {
    color: red;
}

.winner {
    background-color: grey;
}
.scroll{
    display:block;
    margin-top:5px;
    width:220px;
    height:300px;
    overflow:hidden;
    overflow-y:scroll;
}




